.ctm-dropdown {
  position: relative;
  &__header {
    position: relative;
    &:before {
      content: ' ';
      border-left: $dropdownArrow solid transparent;
      border-right: $dropdownArrow solid transparent;
      border-bottom: $dropdownArrow solid $secondClr;
      position: absolute;
      top: calc(100% + #{$dropdownMargin} - #{$dropdownArrow}); // - 1rem is for arrow height
      left: 50%;
      transform: translate(-50%, 0);
      visibility: hidden;
      opacity: 0;
      @include transition(all, 0.8s);
    }
  }
  &__menu {
    position: absolute;
    top: calc(100% + #{$dropdownMargin});
    left: 0;
    right: 0;
    background-color: $dropdownClr;
    border-top: 0.2rem solid $secondClr;
    visibility: hidden;
    opacity: 0;
    @include transition(all, 0.8s);
    z-index: 555;
  }
  &.show {
    .ctm-dropdown {
      &__header {
        &:before {
          visibility: visible;
          opacity: 1;
        }
      }
      &__menu {
        visibility: visible;
        opacity: 1;
      }
    }
  }
}

.ctm-dropdown__opt {
  padding: 1rem 0.5rem;
  cursor: pointer;
}
// main nav hover drop down
.hover-dropdown {
  
  &:hover {
    .hover-dropdown {
      &__header {
        cursor: pointer;
        &:before {
          visibility: visible;
          opacity: 1;
        }
      }
      &__menu {
        visibility: visible;
        opacity: 1;
      }
    }
  }
  &__header {
    position: relative;
    &:before {
      content: ' ';
      border-left: $dropdownArrow solid transparent;
      border-right: $dropdownArrow solid transparent;
      border-bottom: $dropdownArrow solid $secondClr;
      position: absolute;
      top: calc(100% + #{$dropdownMargin2} + 0.7rem - #{$dropdownArrow}); // - 1rem is for arrow height
      left: 50%;
      transform: translate(-50%, 0);
      visibility: hidden;
      opacity: 0;
      @include transition(all, 0.8s);
    }
  }
  &__menu {
    position: absolute;
    top: calc(100% + #{$dropdownMargin2});
    left: 0;
    width: 100%;
    margin: 0 1.5rem;
    background-color: $dropdownClr;
    border-top: 0.2rem solid $secondClr;
    visibility: hidden;
    opacity: 0;
    @include transition(all, 0.8s);
    z-index: 555;
  }
}


